class Snowflake {
  position!: {
    //é›ªèŠ±å¯¹è±¡çš„ä½ç½®;
    x: number //xåæ ‡éšæœº;
    y: number
  }
  speed!: number
  r!: number
  transparency!: number
  color!: {
    r1: number //é›ªèŠ±é¢œè‰²éšæœº;
    g: number
    b: number
  }
  constructor({ width, height }: any) {
    this.init({ width, height }) //æ„é€ å‡½æ•°ï¼Œè°ƒç”¨å®šä¹‰å¥½çš„init()æ–¹æ³•;
  }
  init({ width, height }: any) {
    this.position = {
      //é›ªèŠ±å¯¹è±¡çš„ä½ç½®;
      x: Math.random() * width, //xåæ ‡éšæœº;
      y: Math.random() * height, //yåæ ‡éšæœº;
    }
    this.speed = Math.random() * 10 //é›ªèŠ±ä¸‹è½é€Ÿåº¦ä¸º0-10ä»¥å†…çš„éšæœºæ•°;
    this.r = Math.random() * 6 //é›ªèŠ±çš„åŠå¾„ä¸º0-6ä»¥å†…çš„éšæœºæ•°;
    this.transparency = Math.random() //è®¾ç½®é›ªèŠ±çš„é€æ˜åº¦ä¸ºéšæœº;
    this.color = {
      r1: Math.random() * 255, //é›ªèŠ±é¢œè‰²éšæœº;
      g: Math.random() * 255,
      b: Math.random() * 255,
    }
  }
  draw({ width, height, canvasContext }: any) {
    //é›ªèŠ±ç»˜åˆ¶æ–¹æ³•;
    this.position.y++ //yåæ ‡æ¯æ¬¡é€’å¢1åƒç´ ;
    this.transparency -= 0.01 //é€æ˜åº¦æ¯æ¬¡é€’å‡0.01;
    if (this.transparency <= 0) {
      //é€æ˜åº¦å°äº0ï¼Œå³é›ªèŠ±æ¶ˆå¤±ï¼Œé‡æ–°ç»˜åˆ¶é›ªèŠ±;
      this.init({ width, height, canvasContext })
    }
    canvasContext!.beginPath() //å¼€å§‹ä¸€ä¸ªæ–°çš„å›¾å½¢ç»˜åˆ¶;
    canvasContext!.fillStyle =
      'rgba(' + this.color.r1 + ',' + this.color.g + ',' + this.color.b + ',' + this.transparency + ')' //æ ¹æ®ç±»æ¨¡å‹ç»˜åˆ¶åœ†å½¢é›ªèŠ±;
    canvasContext!.arc(this.position.x, this.position.y, this.r, 0, Math.PI * 2) //å¡«å……é›ªèŠ±çš„é¢œè‰²;
    canvasContext!.fill()
  }
}
export default Snowflake
