import React, { useCallback, useRef } from 'react';
import S from './App.module.css';
import SnowFlak from  './utils/snowFlak'
import {useMounted} from  './hooks/index'
import routes from './routes'
import { HashRouter as Router, Route,Switch } from "react-router-dom";
function App() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const width = window.innerWidth //è·å–ç³»ç»Ÿæ˜¾ç¤ºå®½åº¦;
  const height = window.innerHeight //è·å–ç³»ç»Ÿæ˜¾ç¤ºé«˜åº¦;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const snowArray: { draw: (arg0: { width: number; height: number; canvasContext: CanvasRenderingContext2D | null; }) => void; }[]= [];
  const initSnow = useCallback(() => {
    canvasRef.current!.width = width
    canvasRef.current!.height = height
    for (let i = 0; i < 50; i++) {
      const snow = new SnowFlak({ width, height }) //å®ä¾‹åŒ–1000ä¸ªé›ªèŠ±å¯¹è±¡;
      snowArray.push(snow) //å°†é›ªèŠ±å¯¹è±¡æ·»åŠ åˆ°æ•°ç»„ä¸­;
    }
  }, [height, snowArray, width])
  /**
   * å®šä¹‰åŠ¨ç”»æ•ˆæœ;
   * @return {[type]} [description]
   */
  const cartoon = useCallback(() => {
    if (canvasRef.current) {
      const context = canvasRef.current && canvasRef.current.getContext('2d')
      context && context.clearRect(0, 0, width, height) //åŠ¨ç”»å®Œæˆä¸€æ¬¡è¿›è¡Œæ¸…å±æ“ä½œ;
      for (let j = 0; j < snowArray.length; j++) {
        snowArray[j].draw({ width, height, canvasContext: context }) //å°†å®ä¾‹åŒ–å¥½çš„æ¯ä¸ªé›ªèŠ±å¯¹è±¡åœ¨ç”»å¸ƒä¸Šç”»å‡ºæ¥;
      }
      requestAnimationFrame(cartoon)
    } //é€’å½’è°ƒç”¨åŠ¨ç”»æ•ˆæœ;
  }, [height, snowArray, width])
  useMounted(() => {
    initSnow()
    cartoon()
  })
  return (
    <div className={S.App} style={{height: "100%", width: '100%'}}>
      <canvas className={S.canvas} ref={canvasRef}></canvas>
      <Router>
            <Switch>
                {routes.map((route, key) => (
                    <Route
                        key={key}
                        path={route.path}
                        component={route.component}
                        exact={route.exact}
                    />
                ))}
            </Switch>
        </Router>
    </div>
  );
}

export default App;
