  let i = 0;
  let timer:any;
  // speed in ms
  let deleteDelay = 3000;
  let typeSpeed = 100;
  let delSpeed = 50;
  function typingEffect(el:any) {
    let word = words[i].split("");
    var loopTyping = function () {
      if (word.length > 0) {
        el.innerHTML += word.shift();
      } else {
        delay(function () {
          deletingEffect(el); // do stuff
        }, deleteDelay); // end delay
        // deletingEffect();
        return false;
      }
      timer = setTimeout(loopTyping, typeSpeed);
    };
    loopTyping();
  }

  function deletingEffect(el:any) {
    let word = words[i].split("");
    var loopDeleting = function () {
      if (word.length > 0) {
        word.pop();
        el.innerHTML = word.join("");
      } else {
        if (words.length > i + 1) {
          i++;
        }
        else {
          i = 0;
        }
        typingEffect(el);
        return false;
      }
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      timer = setTimeout(loopDeleting, delSpeed);
    };

    loopDeleting();
  }

  var delay = (function () {
    let timer:any = 0;
    return function (callback:any, ms:number) {
      clearTimeout(timer);
      timer = setTimeout(callback, ms);
    };
  })();
  export default typingEffect